import React from 'react';


import {  HashRouter, Switch, Route, Link } from 'react-router-dom';
import { StyleSheet, css } from 'aphrodite/no-important';
import AppStyle from './css/AppStyle';

const Slider = () => (
    <div id="slider" className={css(AppStyle.slider)}>
        <div className={css(AppStyle.siteTitle)}>
            <h1><Link to='/'>FishBazar63.ru</Link></h1>
            <div className={css(AppStyle.slider_line)}></div>
            <p id="slider_text"></p>

            <p id="slider_text_0" className={css(AppStyle.displayNone)} >...H  ...<br/>         ,      .</p>
            <p id="slider_text_1" className={css(AppStyle.displayNone)} >... ....<br/>         .</p>
            <p id="slider_text_2" className={css(AppStyle.displayNone)} >...P    <br/>    !!..  !</p>
            <p id="slider_text_3" className={css(AppStyle.displayNone)} >...    .<br/>  100%  .</p>
        </div>

    </div>
)
export default Slider;


